
.homehero{
height:100vh;
    width: 100%;
    position: relative;
}


.mask{
    width: 100%;
    height: 100%;
    position: relative;
}
.into_img{
    width: 100%;
        height: 100%;
        background-size:cover;
        background-position: center;
        object-fit: cover;
}
.mask::after{
    content: "";
    width: 100%;
        height: 100%;
        position:absolute;
        top:0;
        left:0;
        /* background-color:rgb(60, 35, 35); */
        /* opacity: 0.8; */

    }



.homehero .content{
position:absolute;
transform: translate(-50%,-50%);
top: 50%; 
left: 50%;
 text-align: center;

}


.homehero .content h1{
    /* font-size: 4rem; */
    padding: 0.6rem 0 1.5rem;
}
.homehero .content p{
    font-size: 1.4rem;
    font-weight: 200;
    /* text-transform: uppercase; */
    color: red;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;


}
content.btn{
    margin: 1rem 0.2rem;
}