@media screen and (max-height: 450px) {
    .overlay {
        overflow-y: auto;
    }

    .overlay a {
        font-size: 20px
    }

    .overlay .closebtn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
}

@media screen and (max-width: 947px) {
    #navComp {
        display: none;
    }

    #navName {
        margin-left: auto
    }

    #nav-menu {
        justify-content: space-between;
    }

    #mobile {
        display: block;
    }
}

@media screen and (max-width: 300px) {
    #navName {
        font-size: 28px;
        margin-left: -30px;
        margin-top: 5px;
    }
}